@font-face {
	font-family: "cmu";
	src: url("../public/fonts/cmu.ttf") format("truetype");
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: "cmu";
	src: url("../public/fonts/cmu-italic.ttf") format("truetype");
	font-weight: normal;
	font-style: italic;
}

@font-face {
	font-family: "cmu";
	src: url("../public/fonts/cmu-bold.ttf") format("truetype");
	font-weight: bold;
	font-style: normal;
}

@font-face {
	font-family: "cmu";
	src: url("../public/fonts/cmu-bold-italic.ttf") format("truetype");
	font-weight: bold;
	font-style: italic;
}

* {
	font-family: "cmu", monospace;
}

html {
	background-color: #FEFCFB;
}

body {
	background-color: #FFF7ED;
	font-family: 'cmu', monospace;
	max-width: 68rem;

	border: 3px dotted #FFC182;
	color: #582D1D;
	margin: 2rem auto;
	padding: 0 2rem;
}

header {
	text-align: center;
}

.container {
	max-width: 960px;
	margin-right: auto;
	margin-left: auto;
	padding-right: 15px;
	padding-left: 15px;
	min-height: 100vh;
	padding: 0.4rem 0 2rem 0;
}

.notes-list {
	display: grid;
	grid-gap: 0.8rem;
	grid-template-columns: repeat(auto-fill,
			minmax(250px, 1fr));
}

textarea {
	resize: none;
	padding: 0.2rem 0.6rem 0;
	background: #FFEFD6;
	border: 2px dotted #FFC182;
	color: #582D1D;
}

textarea:focus {
	outline: none;
	border: 2px solid #FFC182;
}

.add {
	padding: 2px 4px;
	background: #FFDFB5;
	color: #CC4E00;
	border-top: 2px solid #F5AE73;
	border-left: 2px solid #F5AE73;
	position: absolute;
	bottom: 0;
	right: 0;
}

.add:hover {
	cursor: pointer;
	text-decoration: underline;
	text-decoration-style: wavy;
}

.add:active {
	border: none;
	outline: 2px solid #CC4E00;
	text-decoration: underline;
	text-decoration-style: solid;
}

.delete {
	padding: 2px 4px;
	background: #FFDFB5;
	color: #CC4E00;
	border-top: 2px solid #F5AE73;
	border-left: 2px solid #F5AE73;
	position: absolute;
	bottom: 0;
	right: 0;
}

.delete:hover {
	cursor: pointer;
	text-decoration: underline;
	text-decoration-style: wavy;
}

.delete:active {
	border: none;
	outline: 2px solid #CC4E00;
	text-decoration: line-through;
	text-decoration-style: solid;
}

.characters {
	padding: 2px 4px;
	background: #FFDFB5;
	color: #CC4E00;
	border-top: 2px solid #F5AE73;
	border-right: 2px solid #F5AE73;
	position: absolute;
	bottom: 0;
	left: 0;
}

.date {
	padding: 2px 4px;
	background: #FFDFB5;
	color: #CC4E00;
	border-top: 2px solid #F5AE73;
	border-right: 2px solid #F5AE73;
	position: absolute;
	bottom: 0;
	left: 0;
}

.note {
	position: relative;
	padding: 0.6em;
	line-height: 1.3;
	min-height: 208px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	white-space: pre-wrap;
	border: 2px solid #F5AE73;
}

.note-footer {
	display: flex;
	justify-content: space-between;
}

form {
	margin-bottom: 1rem;
}

input {
	font-size: 100%;
	width: 100%;
	padding: 0.6em;
	border: 2px solid #FFC182;
	background-color: #FFEFD6;
	color: #582D1D;
}

::placeholder {
	color: #CC4E00;
}